.App {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(8px + 1vmin);
  color: white;
}

.left-bar {
  position: fixed;
  top: 80px;
  width: 200px;
  padding: 40px;
  height: 100%;
}

.top-container {
  position: fixed;
  width: calc(100% - 400px);
  left: 200px;
  text-align: center;
  z-index: 99;
  display:flex;
  flex-direction: column;
}

.game-container {
  width: calc(100% - 320px);
  padding: 16px 160px ;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

* {
  cursor: none;
}

.cursor {
  position: fixed;
  top: -1000px;
  left: -1000px;
  transform: scale(60%) translate(-80%, -80%);
  will-change: width, height, transform, border;
  z-index: 999;
  pointer-events: none;
}

.inventory {
  top: 16;
  left: 16;
  float: left;
}

.gameboard {
  background-color: #181c14;
  width: 800px;
  height: 800px;
  overflow: hidden;
  position: relative;
}

.grid-wrapper {
  position: relative;
  padding-top: 288px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 440px;
  height: 320px;
  gap: 20px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2px 4px;
  justify-content: space-between;
}

.target {
  width: 80px;
  height: 80px;  
  overflow: hidden;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.faded {
  animation: faded 1s linear forwards;
}

@keyframes faded {
  0% { 
    transform: scaleX(-1) scaleY(100%) translateY(0); 
    opacity: 1;
  }
  3% { 
    transform: scaleX(-1) scaleY(115%) translateY(-10%);
    opacity: 1;
  }
  25% { 
    transform: scaleX(-1) scaleY(60%) translateY(35%);
    opacity: 0.6;
  }
  60% { 
    transform: scaleX(-1) scaleY(60%) translateY(35%);
    opacity: 0.6;
  }
  75% { 
    transform: scaleX(-1) scaleY(60%) translateY(35%);
    opacity: 0;
  }
  100% { 
    transform: scaleX(-1) scaleY(100%) translateY(0); 
    opacity: 0;
  }
}

.inventory table th,td { text-align: center; padding:4px 8px;}
.inventory table td img { height: 40px;}
.inventory table {
  border-collapse: collapse;
  margin: 8px;
  font-size: 0.9em;
  font-family: sans-serif;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}
.inventory th,td {
  border-bottom: thin solid #444444;
}

/* CSS */
.button-3 {
  appearance: none;
  background-color: #2ea44f;
  border: 1px solid rgba(27, 31, 35, .15);
  border-radius: 6px;
  box-shadow: rgba(27, 31, 35, .1) 0 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji";
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  padding: 6px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
  margin: 16px;
}

.button-3:focus:not(:focus-visible):not(.focus-visible) {
  box-shadow: none;
  outline: none;
}

.button-3:hover {
  background-color: #2c974b;
}

.button-3:focus {
  box-shadow: rgba(46, 164, 79, .4) 0 0 0 3px;
  outline: none;
}

.button-3:disabled {
  background-color: #94d3a2;
  border-color: rgba(27, 31, 35, .1);
  color: rgba(255, 255, 255, .8);
  cursor: default;
}

.button-3:active {
  background-color: #298e46;
  box-shadow: rgba(20, 70, 32, .2) 0 1px 0 inset;
}

.scoreboard {
  margin-top: 100px;
}

.target img {
  position: absolute;
  bottom: 0px;
  left: 50%;   
  transform: translateX(-50%);
}

.arm {
  position: absolute;
  bottom: -120px;
  left: 50%;  
  transform: translateX(-50%) scale(60%); 
}